import { NextPage } from 'next';
import { useEffect } from 'react';
import { useRouter } from 'next/router';
import Link from 'next/link';

const HomePage: NextPage = () => {
  const router = useRouter();

  useEffect(() => {
    const token = localStorage.getItem('access_token');
    if (token) {
      router.push('/dashboard'); // Redirige al dashboard si el usuario está autenticado
    }
  }, []);

  return (
    <div style={styles.container}>
      <h1 style={styles.title}>Bienvenido a la Trivia Interactiva</h1>
      <p style={styles.description}>
        ¡Compite en tiempo real y demuestra tus conocimientos!
      </p>

      <div style={styles.links}>
        <Link href="/login" passHref>
          <a style={styles.link}>Iniciar Sesión</a>
        </Link>
        <Link href="/register" passHref>
          <a style={styles.link}>Registrarse</a>
        </Link>
      </div>
    </div>
  );
};

export default HomePage;

// Estilos básicos
const styles = {
  container: {
    display: 'flex',
    flexDirection: 'column',
    alignItems: 'center',
    justifyContent: 'center',
    height: '100vh',
    backgroundColor: '#f0f0f0',
    padding: '20px',
    textAlign: 'center',
  },
  title: {
    fontSize: '2.5rem',
    color: '#333',
    marginBottom: '20px',
  },
  description: {
    fontSize: '1.2rem',
    color: '#666',
    marginBottom: '40px',
  },
  links: {
    display: 'flex',
    gap: '20px',
  },
  link: {
    padding: '10px 20px',
    backgroundColor: '#0070f3',
    color: '#fff',
    borderRadius: '5px',
    textDecoration: 'none',
    fontSize: '1rem',
    transition: 'background-color 0.3s ease',
  },
};